.Pool {
  display: grid;
  gap: 8px;
  justify-content: center;
  grid-template-areas: "t t" "a a" "b c";
  grid-template-columns: 50% 50%;
  border: 2px solid lightgray;
  padding: 16px;
  border-radius: 5px;
}

.Pool h4, .Pool h2 {
  text-align: center;
  margin: 8px 0;
}

.Pool .pool__title {
  grid-area: t;
  width: 100%;
  height: 20px;
  margin: 10;
  text-align: center;
}

.Pool .pool__current {
  min-height: 35px;
  border-radius: 5px;
  grid-area: a;
}

.Pool .pool__edge {
  min-height: 35px;
  border-radius: 5px;
  grid-area: b;
}

.Pool .pool__max {
  min-height: 35px;
  
  border-radius: 5px;
  grid-area: c;
}

.Pool .pool__current input {
  text-align: center !important;
  font-size: 1.4em;
  font-weight: bold;
}

.Pool .pool__edge input {
  text-align: center !important;
}

.Pool .pool__max input {
  text-align: center !important;
}
